import {
  fetchGetGoodsIncomes,
  fetchGoodsIncomes,
  fetchSetStatus
} from '../api';
import React, { useEffect, useState } from 'react';
import { Spin, Table, Alert, Input, Select } from 'antd';
import { useRef } from 'react';
import moment from 'moment';

const Delivery = ({ currentWbKey }) => {
  const { Option } = Select;
  const idRef = useRef(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [goods, setGoods] = useState([]);
  const [detail, setDetail] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
    showSizeChanger: true
  });

  const statuses = [' ', '   WB', '  '];

  const getDeatil = async (id) => {
    try {
      setLoading(true);
      const res = await fetchGetGoodsIncomes(id);
      if (res.incomes) {
        setDetail(res.incomes);
      } else {
        setError(res.detail);
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  const editDetail = async ({ id, status, number }) => {
    try {
      setLoading(true);
      const res = await fetchSetStatus(id || idRef.current, { status, number });
      if (res.incomes) {
        setDetail(res.incomes);
      } else {
        setError(res.detail);
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  const getGoodsList = async (pagination) => {
    try {
      setLoading(true);
      const res = await fetchGoodsIncomes({
        wbKey: currentWbKey,
        limit: pagination?.pageSize,
        offset: (pagination?.current - 1) * pagination?.pageSize || null
      });
      if (res.results) {
        setGoods(res.results);
        setPagination((prev) => {
          return {
            ...prev,
            pageSize: pagination?.pageSize,
            current: pagination?.current,
            total: Math.ceil(res.count)
          };
        });
      } else {
        setError(res.detail);
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getGoodsList();
  }, [currentWbKey]);

  const columns = [
    {
      title: '',
      dataIndex: 'date',
      key: 'date',
      render: (date) => {
        return <p>{moment(date).format('YYYY-MM-DD')}</p>;
      }
    },
    {
      title: '',
      dataIndex: 'warehouse_name',
      key: 'warehouse_name'
    },
    {
      title: '-',
      dataIndex: 'quantity',
      key: 'quantity'
    },
    {
      title: ' ',
      dataIndex: 'quantity',
      key: 'quantity',

      render: (_, record) => (
        <Input
          placeholder=" "
          defaultValue={record.number}
          onPressEnter={(e) => {
            editDetail({ id: record.id, number: e.target.value });
            getDeatil(e.target.value);
          }}
          onBlur={(e) => {
            editDetail({ id: record.id, number: e.target.value });
            getDeatil(e.target.value);
          }}
        />
      )
    }
  ];

  const columnsDetail = [
    {
      title: '',
      dataIndex: 'article',
      key: 'article'
    },
    {
      title: '',
      dataIndex: 'status',
      key: 'status',
      render: (_, record) => (
        <Select
          defaultValue={record.status}
          value={record.status}
          placeholder=" "
          onChange={(value) => {
            editDetail({ status: value });
          }}
          style={{
            width: 150
          }}
        >
          {statuses?.map((item, i) => (
            <Option key={i} value={item}>
              {item}
            </Option>
          ))}
        </Select>
      )
    },
    {
      title: 'item_name',
      dataIndex: 'item_name',
      key: 'item_name'
    },
    {
      title: '-',
      dataIndex: 'quantity',
      key: 'quantity'
    }
  ];

  return (
    <>
      <div style={{ display: 'flex', gap: 16 }}>
        <Spin spinning={loading}>
          <Table
            size="small"
            scroll={{ x: true }}
            sticky={{ offsetHeader: 140 }}
            columns={columns}
            dataSource={goods}
            pagination={pagination}
            onChange={getGoodsList}
          />
        </Spin>
        <Spin spinning={loading}>
          <Table
            size="small"
            scroll={{ x: true }}
            columns={columnsDetail}
            dataSource={detail}
            pagination={false}
            sticky={{ offsetHeader: 140 }}
          />
        </Spin>
      </div>
      {!!error && <Alert closable message={error} type="error" />}
    </>
  );
};

export default Delivery;
